import { gql } from 'apollo-boost'

export const getPostsQuery = gql`
  query($startWithId: ID) {
    posts(startWithId: $startWithId) {
      text
      id
      createdAt
      file {
        id
        url
        type
      }
      user {
        name
        gender
        id
      }
    }
  }
`

export const getUsersQuery = gql`
  {
    users {
      name
      id
      gender
      error
    }
  }
`

export const getUserName = gql`
  query($id: ID!) {
    user(id: $id) {
      name
    }
  }
`
