import React from 'react'
import './VideoBackground.css'
import video from '../../assets/180825_01_laptop.mp4'
import Context from '../../context/Context'

const VideoBackground = ({ containerRef }) => {
  const [postsContainerWidth, setPostsContainerWidth] = React.useState(null)
  const [postsContainerHeight, setPostsContainerHeight] = React.useState(null)
  const videoRef = React.useRef(null)
  const { formIsClear } = React.useContext(Context)

  React.useEffect(() => {
    setSize()
    window.addEventListener('resize', () => {
      setSize()
    })
    return () => {
      window.removeEventListener('resize', setSize())
    }
  }, [])

  React.useEffect(
    () => {
      formIsClear ? pauseVideo() : playVideo()
    },
    [formIsClear]
  )

  const setSize = () => {
    setPostsContainerWidth(
      containerRef.current && containerRef.current.clientWidth
    )
    setPostsContainerHeight(
      containerRef.current && containerRef.current.clientHeight
    )
  }

  const playVideo = () => {
    videoRef.current && videoRef.current.play()
  }
  const pauseVideo = () => {
    videoRef.current && videoRef.current.pause()
  }

  return (
    <div className="video-background__container">
      <div className="video-background__overlay" />
      <video
        className="video-background__video"
        ref={videoRef}
        loop
        muted
        src={video}
        height={
          postsContainerHeight &&
          postsContainerHeight * 1.89 > postsContainerWidth
            ? postsContainerHeight
            : ''
        }
        width={
          postsContainerWidth &&
          postsContainerWidth > postsContainerHeight * 1.89
            ? '100%'
            : ''
        }
      />
    </div>
  )
}

export default VideoBackground
