import React from 'react'

const MyButton = ({ className, onClick, text, children, focus }) => {
  const ref = React.useRef(null)

  React.useEffect(() => {
    focus && ref.current && ref.current.focus()
  }, [])
  const handleKeyPress = e => {
    e.charCode === 13 && onClick()
  }

  return (
    <div
      className={`general-button ${className}`}
      tabIndex="0"
      onKeyPress={handleKeyPress}
      onClick={onClick}
      ref={ref}
    >
      {children}
      {text}
    </div>
  )
}

export default MyButton
