import { gql } from 'apollo-boost'

export const addPostMutation = gql`
  mutation($userId: ID!, $text: String!, $fileId: ID, $fileUrl: String, $fileType: String) {
    addPost(userId: $userId, text: $text, fileId: $fileId, fileUrl: $fileUrl, fileType: $fileType) {
      text
      id
      createdAt 
      user {
        name
        gender
        id
      }
      file {
        id
        url
        type
      }
    }
  }
`
export const addUserMutation = gql`
  mutation(
    $name: String!
    $gender: String!
    $email: String!
    $password: String!
  ) {
    addUser(name: $name, gender: $gender, email: $email, password: $password) {
      name
      id
      gender
      error
    }
  }
`

export const deletePostMutation = gql`
  mutation($id: ID!) {
    deletePost(id: $id) {
      id
    }
  }
`
export const deleteUserMutation = gql`
  mutation($id: ID!) {
    deleteUser(id: $id) {
      name
      id
      gender
      error
    }
  }
`
