import React from 'react'
import './Post.css'
import { FaTimes } from 'react-icons/fa'
import { Mutation } from 'react-apollo'
import { deletePostMutation } from '../../apollo/mutations'
import Context from '../../context/Context'
import moment from 'moment'
import MyButton from '../MyButton/MyButton'
import Divider from '../Divider/Divider'
import firebase from '../../firebase'
import ErrorMessage from '../ErrorMessage/ErrorMessage'
import Loader from '../Loader/Loader'
import UploadedFile from '../UploadedFile/UploadedFile'
import Confirm from '../Confirm/Confirm'

const Post = ({ post, setFileLoaded, scrollTop }) => {
  const { profileState } = React.useContext(Context)
  const [error, setError] = React.useState(false)
  const [loader, setLoader] = React.useState(false)
  const [showConfirm, setShowConfirm] = React.useState(false)

  const deletePost = async (deletePostMutation, id) => {
    setLoader(true)
    try {
      if (post.file.url) {
        const storageRef = firebase.storage().ref(`files/${post.file.id}`)
        await storageRef.delete()
      }
      await deletePostMutation({
        variables: { id }
      })
    } catch (error) {
      console.log(error)
      setError(new Error('There is a problem deleting the file...').message)
      setLoader(false)
    }
  }
  const handleDeletePostClick = () => {
    setShowConfirm(true)
  }
  const handleConfirmClick = (deletePostMutation, id) => {
    deletePost(deletePostMutation, id)
    setShowConfirm(false)
  }
  const handleConfirmCancelClick = () => {
    setShowConfirm(false)
  }
  const deleteIcon = <FaTimes color="red" cursor="pointer" />
  return (
    <Mutation mutation={deletePostMutation}>
      {(deletePost, { error: deletePostError }) => (
        <>
          <div className="post__container">
            <div className="post__detailes-container">
              <div className="post__created-at-container">
                {moment(post.createdAt).calendar()}
              </div>
              <div className="post__user-name">
                <span>By: </span>
                {post.user.name}
              </div>
            </div>
            <Divider />
            {error && <ErrorMessage error={error} />}
            {deletePostError && (
              <ErrorMessage
                error={
                  new Error('There is a problem deleting the message...')
                    .message
                }
              />
            )}
            <div className="post__text-row">
              <div className="post__invisible-sides" />
              {profileState.currentUserId === post.user.id && (
                <div className="post__invisible" />
              )}

              <div className="post__visible">
                <div
                  className={
                    profileState.currentUserId === post.user.id
                      ? 'post__text post__text--own'
                      : 'post__text post__text--else'
                  }
                >
                  {post.file.url && (
                    <UploadedFile
                      fileUrl={post.file.url}
                      handleFileLoaded={setFileLoaded}
                      fileType={post.file.type}
                      posted
                      inverted={profileState.currentUserId === post.user.id}
                    />
                  )}
                  <div className="post__text-and-button">
                    {loader ? (
                      <Loader />
                    ) : (
                      <span className="post__text-span">{post.text}</span>
                    )}
                    <MyButton
                      className={
                        profileState.currentUserId !== post.user.id
                          ? 'post__delete-button post__delete-button--hidden'
                          : 'post__delete-button'
                      }
                      onClick={handleDeletePostClick}
                      children={deleteIcon}
                    />
                  </div>
                </div>
              </div>
              {profileState.currentUserId !== post.user.id && (
                <div className="post__invisible" />
              )}
              <div className="post__invisible-sides" />
            </div>
          </div>
          {showConfirm && (
            <Confirm
              top={scrollTop + 20}
              modal
              text="Are you sure you want to delete the post?"
              onCancelClick={handleConfirmCancelClick}
              onConfirmClick={() => handleConfirmClick(deletePost, post.id)}
            />
          )}
        </>
      )}
    </Mutation>
  )
}

export default Post
