import React from 'react'
import './Confirm.css'
import MyButton from '../MyButton/MyButton'
import Divider from '../Divider/Divider'

const Confirm = ({
  text = 'Are you sure?',
  confirmText = 'Delete',
  onConfirmClick,
  onCancelClick,
  top = 0,
  modal
}) => {
  return (
    <div className={modal ? "confirm__container confirm__container--modal" : "confirm__container "} style={{ top: top }}>
      <Divider />
      <div className="confirm__message">{text}</div>
      <Divider />
      <div className="confirm__buttons-container">
        <MyButton text="Cancel" onClick={onCancelClick} focus/>
        <MyButton text={confirmText} onClick={onConfirmClick} />
      </div>
    </div>
  )
}

export default Confirm
