import React from 'react'
import './Posts.css'
import { Query } from 'react-apollo'
import { getPostsQuery } from '../../apollo/queries'
import { postChangeSubscription } from '../../apollo/subscriptions'
import Context from '../../context/Context'
import PostsList from '../PostsList/PostsList'

const Posts = () => {
  const { profileState } = React.useContext(Context)
  const [subscriptionHappened, setSubscriptionHappened] = React.useState(false)

  const subscribeToNewPosts = subscribeToMore => {
   const unsubscribe = subscribeToMore({
      document: postChangeSubscription,
      updateQuery: (prev, { subscriptionData }) => {
        if (!subscriptionData.data) return prev
        setSubscriptionHappened(true)
        return { posts: subscriptionData.data.postChange }
      }
    })
    return unsubscribe
  }

  return profileState.userLoggedIn ? (
    <>
      <Query
        query={getPostsQuery}
        variables={{ startWithId: null }}
        notifyOnNetworkStatusChange
      >
        {({
          data: { posts },
          refetch,
          fetchMore,
          subscribeToMore,
          error
        }) => {
          return (
            <>
            <PostsList
              subscribeToNewPosts={() => subscribeToNewPosts(subscribeToMore)}
              posts={posts}
              refetch={refetch}
              fetchMore={fetchMore}
              subscriptionHappened={subscriptionHappened}
              setSubscriptionHappened={setSubscriptionHappened}
              error={error}
            />
            </>
          )
        }}
      </Query>
    </>
  ) : (
    <span />
  )
}

export default Posts
