import { useState, useEffect } from 'react'
const initialMediaQueries = {
  mobile: '(max-width: 767px)',
  tablet: '(min-width: 768px) and (max-width: 991px)',
  desktop: '(min-width: 992px) and (max-width: 1199px)',
  largeDesktop: '(min-width: 1200px)'
}
const useMedia = (mediaQueries = initialMediaQueries) => {

  const mediaQueriesKeys = Object.keys(mediaQueries || {})
  const mediaQueriesValues = mediaQueriesKeys.map(key => mediaQueries[key])
  const useStatesArr = mediaQueriesValues.map(query =>
    useState(window.matchMedia(query))
  )
  useEffect(() => {
    const mediaListsArr = mediaQueriesValues.map(value =>
      window.matchMedia(value)
    )

    const listenersArr = useStatesArr.map((useState, i) => () =>
      useState[1](mediaListsArr[i].matches)
    )
    mediaListsArr.map((mediaList, i) => mediaList.addListener(listenersArr[i]))
    listenersArr.map(listener => listener())

    return () => {
      mediaListsArr.map((mediaList, i) =>
        mediaList.removeListener(listenersArr[i])
      )
    }
  }, [])

  const result = {}
  mediaQueriesKeys.map((key, i) => (result[key] = useStatesArr[i][0]))
  return result
}
export default useMedia
