import React from 'react'
import './SignupForm.css'
import { Mutation } from 'react-apollo'
import { addUserMutation } from '../../apollo/mutations'
import useInput from '../../hooks/useInput'
import Context from '../../context/Context'
import firebase from '../../firebase'
import Loader from '../Loader/Loader'
import MyButton from '../MyButton/MyButton'

const SignupForm = () => {
  const { reset: resetName, ...name } = useInput('')
  const { reset: resetEmail, ...email } = useInput('')
  const { reset: resetPassword, ...password } = useInput('')
  const gender = useInput('male')
  const { profileDispatch } = React.useContext(Context)
  const [loading, setLoading] = React.useState(false)

  const handleSignupClick = async addUser => {
    setLoading(true)
    try {
      await addUser({
        variables: {
          name: name.value,
          gender: gender.value,
          email: email.value,
          password: password.value
        }
      })
      await firebase
        .auth()
        .signInWithEmailAndPassword(email.value, password.value)
    } catch (error) {
      const errorMessage = error.message
      profileDispatch({ type: 'SET_ERROR', error: errorMessage })
      setLoading(false)
    }
  }

  const handleLoginClick = () => {
    profileDispatch({ type: 'TOOGLE_FORM_TYPE' })
  }

  const validateForm = () =>
    name.value.length !== 0 &&
    email.value.length !== 0 &&
    password.value.length !== 0
    
  return (
    <div className="signup-form__container">
      {loading ? (
        <Loader text="Signing up user..." />
      ) : (
        <>
          <div className="signup-form__header">SIGN UP</div>
          <input placeholder="Name" {...name} />
          <input placeholder="Email" {...email} />
          <input placeholder="Password" type="password" {...password} />
          <div className="signup-form__radios-container">
            <div className="signup-form__radio">
              <input
                type="radio"
                onChange={gender.onChange}
                value="male"
                checked={gender.value === 'male'}
              />
              <div className="singup-form__radio-divider" />
              <label className="singup-form__radio-label">Male</label>
            </div>
            <div className="signup-form__radio">
              <input
                type="radio"
                onChange={gender.onChange}
                value="female"
                checked={gender.value === 'female'}
              />
              <div className="singup-form__radio-divider" />
              <label className="singup-form__radio-label">Female</label>
            </div>
          </div>

          <Mutation mutation={addUserMutation}>
            {addUser => (
              <div>
                <div className="signup-form__buttons-container">
                  <MyButton
                    text="Log In"
                    onClick={handleLoginClick}
                  />
                  {validateForm() && (
                    <MyButton
                      text="Signup"
                      onClick={() => handleSignupClick(addUser)}
                    />
                  )}
                </div>
              </div>
            )}
          </Mutation>
        </>
      )}
    </div>
  )
}

export default SignupForm
