import React from 'react'
import './FileUploader.css'
import uuid from 'uuid'
import firebase from '../../firebase'
import Dropzone from 'react-dropzone'
import MyButton from '../MyButton/MyButton'
import { MdAttachFile } from 'react-icons/md'
import { FaPlus } from 'react-icons/fa'

const FileUploader = ({
  setShowLoader,
  setError,
  setFileId,
  setFileUrl,
  setFileType
}) => {
  const handleFileUpload = async (files, rejectedFiles) => {
    setShowLoader(true)
    setError(null)
    const file = files[0]
    const fileId = uuid.v4()
    const storageRef = firebase.storage().ref(`files/${fileId}`)
    try {
      if (rejectedFiles && rejectedFiles.length > 0)
        throw new Error(
          'The file has been rejected: (the file must be a media files and the maximum file size is 25MB)'
        )
      await storageRef.put(file)
      const url = await storageRef.getDownloadURL()
      setFileId(fileId)
      setFileUrl(url)
      setFileType(file.type)
    } catch (error) {
      console.log(error)
      setError(new Error(error.message))
      setShowLoader(false)
    }
  }

  return (
    <Dropzone
      accept="image/*, audio/*, video/*"
      maxSize={25000000}
      multiple={false}
      onDrop={handleFileUpload}
    >
      {({ getRootProps, getInputProps, isDragActive }) => (
        <div {...getRootProps()}>
          <MyButton
            className="file-uploader__file-upload-button"
            children={
              <>
                {isDragActive ? (
                  <div className="file-uploader__drop-files-box ">
                    <div className="file-uploader__drop-files-box-sides">
                      Drop files here...
                    </div>
                    <FaPlus color="var(--mainColor)" size="5rem" />
                    <div className="file-uploader__drop-files-box-sides" />
                  </div>
                ) : (
                  <MdAttachFile
                    className="file-uploader__attach-icon"
                    color="var(--mainColor)"
                    size="32px"
                  />
                )}
                <input {...getInputProps()} />
              </>
            }
          />
        </div>
      )}
    </Dropzone>
  )
}

export default FileUploader
