import React from 'react'
import './UsersList.css'
import User from '../User/User'
import Loader from '../Loader/Loader'
import ErrorMessage from '../ErrorMessage/ErrorMessage'
import Context from '../../context/Context'
const UsersList = ({ users, refetch, loading, error, visiable }) => {
  const { profileState } = React.useContext(Context)
  React.useEffect(() => {
    refetch()
  }, [])

  return loading ? (
    <div className="users-list__container">
      <Loader withRadius text="Loading users..." />
    </div>
  ) : visiable ? (
    <div className="users-list__container">
      {error && (
        <ErrorMessage
          error={new Error('There is a problem getting the users...').message}
        />
      )}
      {users.map(
        user =>
          profileState.currentUserId !== user.id && (
            <User user={user} key={user.id} />
          )
      )}
    </div>
  ) : (
    <span />
  )
}

export default UsersList
