import React, { useState } from 'react'
import './NewPosts.css'
import { Mutation, Query } from 'react-apollo'
import { getUserName } from '../../apollo/queries'
import { addPostMutation } from '../../apollo/mutations'
import Context from '../../context/Context'
import useMedia from '../../hooks/useMedia'
import firebase from '../../firebase'
import { Form, TextArea } from 'semantic-ui-react'
import Loader from '../Loader/Loader'
import ErrorMessage from '../ErrorMessage/ErrorMessage'
import FileUploader from '../FileUploader/FileUploader'
import UploadedFile from '../UploadedFile/UploadedFile'

const NewPosts = () => {
  const [text, setText] = useState('')
  const [fileId, setFileId] = useState('')
  const [fileUrl, setFileUrl] = useState('')
  const [fileType, setFileType] = useState('')
  const [showLoader, setShowLoader] = useState(false)
  const [error, setError] = useState(null)
  const { profileState, typingDispatch } = React.useContext(Context)
  const { mobile, tablet } = useMedia()
  const textAreaRef = React.useRef(null)

  React.useEffect(() => {
    textAreaRef.current.focus()
  }, [])
  const handleTextChange = (e, beforeChangeText) => {
    if (beforeChangeText.length === 0 && e.target.value.length > 0)
      typingDispatch({ type: 'TYPING_STARTED' })
    if (e.target.value.length === 0 && beforeChangeText.length > 0)
      typingDispatch({ type: 'FORM_IS_CLEAR' })
    setText(e.target.value)
  }
  const handlePostClick = async mutation => {
    setShowLoader(true)
    await mutation()
    setText('')
    setFileId('')
    setFileUrl('')
    setFileType('')
    setError(null)
    setShowLoader(false)
    typingDispatch({ type: 'FORM_IS_CLEAR' })
  }

  const handleEnterKeyPress = (e, mutation) => {
    validateText() || fileUrl
      ? e.preventDefault() || handlePostClick(mutation)
      : e.preventDefault()
  }

  const validateText = () => text.match(/\S.*/g)

  const handleFileDeleteClick = async () => {
    setShowLoader(true)
    try {
      const storageRef = firebase.storage().ref(`files/${fileId}`)
      await storageRef.delete()
      setFileId('')
      setFileUrl('')
      setFileType('')
      setShowLoader(false)
    } catch (error) {
      console.error(error)
      setError(new Error(`There is a problem deleting the file ...`))
      setShowLoader(false)
    }
  }

  const handleFileLoaded = () => {
    setShowLoader(false)
  }

  return (
    <Mutation
      mutation={addPostMutation}
      variables={{
        userId: profileState.currentUserId,
        text,
        fileId,
        fileUrl,
        fileType
      }}
    >
      {(mutation, { error: mutationError }) => (
        <div className="new-posts__container">
          <Query
            query={getUserName}
            variables={{ id: profileState.currentUserId }}
          >
            {({ loading, data }) => (
              <>
                {error && <ErrorMessage error={error.message} />}
                {mutationError && (
                  <ErrorMessage
                    error={
                      new Error('There is a problem sending the message...')
                        .message
                    }
                  />
                )}
                {fileUrl && (
                  <UploadedFile
                    fileUrl={fileUrl}
                    handleFileLoaded={handleFileLoaded}
                    handlePostClick={handlePostClick}
                    mutation={mutation}
                    handleFileDeleteClick={handleFileDeleteClick}
                    fileType={fileType}
                  />
                )}
                <Form className="new-posts__form">
                  <TextArea
                    rows={2}
                    autoHeight
                    className={`new-posts__text-area ${
                      mobile
                        ? 'new-posts__text-area--mobile'
                        : tablet
                        ? 'new-posts__text-area--tablet'
                        : 'new-posts__text-area--desktop-largeDesktop'
                    }`}
                    onChange={e => {
                      handleTextChange(e, text)
                    }}
                    onKeyPress={e => {
                      e.charCode === 13 && handleEnterKeyPress(e, mutation)
                    }}
                    value={text}
                    ref={textAreaRef}
                    placeholder={`Type a message${
                      !loading && data.user ? ', ' + data.user.name : ''
                    } ...`}
                  />
                </Form>
                {showLoader ? (
                  <Loader inverted />
                ) : (
                  !fileUrl && (
                    <FileUploader
                      setShowLoader={setShowLoader}
                      setError={setError}
                      setFileId={setFileId}
                      setFileUrl={setFileUrl}
                      setFileType={setFileType}
                    />
                  )
                )}
                <button
                  className={
                    validateText() || fileUrl
                      ? 'new-posts__button'
                      : 'new-posts__button new-posts__button--disabled'
                  }
                  onClick={() => {
                    handlePostClick(mutation)
                  }}
                  disabled={!validateText() && !fileUrl}
                >
                  Send
                </button>
              </>
            )}
          </Query>
        </div>
      )}
    </Mutation>
  )
}

export default NewPosts
