import React from 'react'
import './AppRouter.css'
import {
  HashRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom'
import App from '../components/App/App'
import apolloClient from '../apollo/apolloSetup'
import { ApolloProvider } from 'react-apollo'
import { ContextProvider } from '../context/Context'
import Context from '../context/Context'
import Profile from '../components/Profile/Profile'
import ErrorBoundary from '../components/ErrorBoundary/ErrorBoundary'
import VideoBackground from '../components/VideoBackground/VideoBackground'

const Login = () => {
  const { profileState } = React.useContext(Context)
  const containerRef = React.useRef(null)

  return profileState.userLoggedIn ? (
    <Redirect to="/" />
  ) : (
    <div className="app-router__profile-container" ref={containerRef}>
      <VideoBackground containerRef={containerRef} />
      <div className="app-router__divider" />
      <div className="app-router__profile-wrapper">
        <Profile />
      </div>
    </div>
  )
}

const PrivateRoute = ({ component: Component, ...rest }) => {
  const { profileState } = React.useContext(Context)
  return (
    <Route
      {...rest}
      render={props =>
        profileState.userLoggedIn ? (
          <ErrorBoundary>
            <Component {...props} {...rest} />
          </ErrorBoundary>
        ) : (
          <Redirect to="/login" />
        )
      }
    />
  )
}
const AppRouter = () => {
  return (
    <ApolloProvider client={apolloClient}>
      <ContextProvider>
        <Router>
          <Switch>
            <Route path="/login" component={Login} />
            <PrivateRoute path="/" component={App} />
          </Switch>
        </Router>
      </ContextProvider>
    </ApolloProvider>
  )
}
export default AppRouter
