import React from 'react'
import './UploadedFile.css'
import { FaTimes, FaFileAlt, FaPlay, FaPause } from 'react-icons/fa'
import MyButton from '../MyButton/MyButton'
import { MdFullscreen } from 'react-icons/md'

const UploadedFile = ({
  fileUrl,
  handleFileLoaded,
  handleFileDeleteClick,
  fileType,
  posted,
  inverted
}) => {
  const imageRef = React.useRef(null)
  const videoRef = React.useRef(null)
  const [playing, setPlaying] = React.useState(false)
  React.useEffect(() => {
    handleFileLoaded()
  }, [])
 
  const handleImageClick = () => {
    goFullScreen(imageRef)
  }
  const handleVideoFullscreenClick = () => {
    goFullScreen(videoRef)
  }

  const goFullScreen = ref => {
    if (ref.current) {
      ref.current.requestFullscreen
        ? ref.current.requestFullscreen()
        : ref.current.mozRequestFullScreen
        ? ref.current.mozRequestFullScreen()
        : ref.current.webkitRequestFullScreen
        ? ref.current.webkitRequestFullScreen()
        : ref.current.msRequestFullscreen && ref.current.msRequestFullscreen()
    }
  }

  const playVideo = () => {
    videoRef.current && videoRef.current.play()
    setPlaying(true)
  }

  const pauseVideo = () => {
    videoRef.current && videoRef.current.pause()
    setPlaying(false)
  }

  let uploadedFilePreview
  if (fileType && fileType.includes('image'))
    uploadedFilePreview = (
      <img
      className={posted ? "uploaded-file__image uploaded-file__image--posted" : "uploaded-file__image"}

        src={fileUrl}
        alt="uploaded"
        onLoad={handleFileLoaded}
        onClick={handleImageClick}
        ref={imageRef}
      />
    )

  if (fileType && fileType.includes('video'))
    uploadedFilePreview = (
      <div className="uploaded-file__video-container ">
        <video ref={videoRef} width="150px">
          <source src={fileUrl} /> Your browser does not support HTML5 video.
        </video>

        <MyButton
          className="uploaded-file__play-pause-button"
          onClick={playing ? pauseVideo : playVideo}
          children={playing ? <FaPause /> : <FaPlay />}
        />
        <MyButton
          className="uploaded-file__fullscreen-button"
          onClick={handleVideoFullscreenClick}
          children={<MdFullscreen />}
        />
      </div>
    )

  if (fileType && fileType.includes('audio'))
    uploadedFilePreview = <audio src={fileUrl} controls />

  return (
    <div className="uploaded-file__file-container">
      {uploadedFilePreview || (
        <div
          className={
            inverted
              ? 'uploaded-file__general-file-container uploaded-file__general-file-container--inverted'
              : 'uploaded-file__general-file-container'
          }
        >
          <a href={fileUrl} download target="_blank" rel="noopener noreferrer">
            <FaFileAlt color={inverted ? 'white' : '0084ff'} size="3rem" />
          </a>
          <div>Attached file</div>
        </div>
      )}
      {!posted && (
        <FaTimes color="red" cursor="pointer" onClick={handleFileDeleteClick} />
      )}
    </div>
  )
}

export default UploadedFile
