import React from 'react'
import { profileReducer, profileInitialState } from './reducers/profileReducer'
import { typingReducer, typingInitialState } from './reducers/typingReducer'

const Context = React.createContext()

export const ContextProvider = ({ children }) => {
  const Provider = Context.Provider

  const [profileState, profileDispatch] = React.useReducer(
    profileReducer,
    profileInitialState
  )
  const [typingState, typingDispatch] = React.useReducer(
    typingReducer,
    typingInitialState
  )
  return (
    <Provider value={{ profileState, profileDispatch, ...typingState, typingDispatch }}>{children}</Provider>
  )
}

export default Context
