export const typingInitialState = {
  formIsClear: true
}

export const typingReducer = (state, action) => {
  switch (action.type) {
    case 'TYPING_STARTED':
      return {
        ...state,
        formIsClear: false
      }
    case 'FORM_IS_CLEAR':
      return {
        ...state,
        formIsClear: true
      }
    default:
      return state
  }
}
