import React from 'react'
import './PostsList.css'
import Post from '../Post/Post'
import Loader from '../Loader/Loader'
import Divider from '../Divider/Divider'
import { Visibility } from 'semantic-ui-react'
import ErrorMessage from '../ErrorMessage/ErrorMessage'

const PostsList = ({
  posts,
  refetch,
  fetchMore,
  subscribeToNewPosts,
  subscriptionHappened,
  setSubscriptionHappened,
  error
}) => {
  const containerRef = React.useRef(null)
  const [loadMore, setLoadMore] = React.useState(true)
  const [fileLoaded, setFileLoaded] = React.useState(true)
  const [topScroll, setTopScroll] = React.useState(0)
  const afterRender = React.useRef(true)
  React.useEffect(
    () => {
      if (
        (containerRef.current && subscriptionHappened) ||
        (containerRef.current && fileLoaded)
      ) {
        containerRef.current.scrollTop = containerRef.current.scrollHeight
        setSubscriptionHappened(false)
        setLoadMore(true)
        setFileLoaded(false)
      }
    },
    [posts, fileLoaded]
  )

  React.useEffect(() => {
    if (containerRef.current && afterRender.current) {
      containerRef.current.scrollTop = containerRef.current.scrollHeight
      afterRender.current = false
    }
  })

  React.useEffect(() => {
    if (containerRef.current)
      containerRef.current.scrollTop = containerRef.current.scrollHeight
    refetch()
  }, [])

  React.useEffect(() => {
    const unsubscribeToMore = subscribeToNewPosts()
    return () => {
      unsubscribeToMore()
    }
  }, [])

  const handleLoadMore = () => {
    fetchMore({
      variables: {
        startWithId: posts[posts.length - 1].id
      },
      updateQuery: (prev, { fetchMoreResult }) => {
        fetchMoreResult.posts.length < 20
          ? setLoadMore(false)
          : setLoadMore(true)
        return { posts: [...prev.posts, ...fetchMoreResult.posts] }
      }
    })
    const heightBefore = containerRef.current.scrollHeight
    setTimeout(() => {
      containerRef.current.scrollTop =
        containerRef.current.scrollHeight - heightBefore
    }, 1000)
  }
  const handleScroll = () => {
    setTopScroll(containerRef.current.scrollTop)
  }

  return posts ? (
    <div
      className="posts-list__container"
      ref={containerRef}
      onScroll={handleScroll}
    >
      <div
        className={
          posts.length < 1
            ? 'posts-list__container-inner posts-list__container-inner--empty'
            : 'posts-list__container-inner'
        }
      >
        {posts.length < 1 && (
          <div className="posts-list__start-element">
            Start Typing To Chat ...
          </div>
        )}
        {posts.map(post => (
          <React.Fragment key={post.id}>
            <Post
              post={post}
              setFileLoaded={() => {
                setFileLoaded(true)
              }}
              scrollTop={containerRef.current ? topScroll : 0}
            />
            <Divider />
          </React.Fragment>
        ))}

        <Visibility
          once={false}
          onBottomVisible={() => {
            loadMore && posts.length > 19 && handleLoadMore()
          }}
        />
        {loadMore && posts.length > 19 && (
          <div className="post-list__loader-wrapper">
            <Loader withRadius text="Loading more messages..." />
          </div>
        )}
        {error && (
          <ErrorMessage
            error={
              new Error('There is a problem loading the messages...').message
            }
          />
        )}
      </div>
    </div>
  ) : (
    <div className="post-list__loader-wrapper post-list__loader-wrapper--empty">
      <span />
    </div>
  )
}

export default PostsList
