import React from 'react'
import './Loader.css'

const Loader = ({ text, withRadius, inverted }) => {
  return (
    <div
      className={`loader__container ${withRadius &&
        `loader__container--withRadius`} ${inverted &&
        `loader__container--inverted`}`}
    >
      <div
        className={`loader__spinner ${inverted && `loader__spinner--inverted`}`}
      />
      <div>{text}</div>
    </div>
  )
}
export default Loader
