import React from 'react'
import './User.css'
import { FaMale, FaFemale } from 'react-icons/fa'
import Divider from '../Divider/Divider'
import Context from '../../context/Context'

const User = ({ user }) => {
  const { profileState } = React.useContext(Context)
  return (
    <div className="user__container">
      <div
        className={
          profileState.currentUserId === user.id
            ? 'user__user-name-container user__user-name-container--current'
            : 'user__user-name-container'
        }
      >
        {user.gender === 'male' ? <FaMale /> : <FaFemale />}
        <div>{user.name}</div>
      </div>
      <Divider />
    </div>
  )
}

export default User
