import { gql } from 'apollo-boost'

export const postChangeSubscription = gql`
  subscription {
    postChange {
      text
      id
      createdAt
      user {
        name
        gender
        id
      }
      file {
        id
        url
        type
      }
    }
  }
`

export const userChangeSubscription = gql`
  subscription {
    userChange {
      name
      id
      gender
      error
    }
  }
`
