import React from 'react'
import { Query, Subscription } from 'react-apollo'
import { getUsersQuery } from '../../apollo/queries'
import { userChangeSubscription } from '../../apollo/subscriptions'
import UsersList from '../UsersList/UsersList'

const Users = ({ visiable }) => {
  return (
    <Query query={getUsersQuery}>
      {({ loading, data, refetch, error }) => (
        <Subscription subscription={userChangeSubscription}>
          {({ loading: userChangeLoading, data: userChangeData }) => {
            const users = !userChangeLoading
              ? userChangeData.userChange
              : data.users
            return  (
              <UsersList
                users={users}
                refetch={refetch}
                loading={loading}
                error={error}
                visiable={visiable}
              />
            ) 
          }}
        </Subscription>
      )}
    </Query>
  )
}

export default Users
