import React from 'react'
import './Profile.css'
import { FaUser } from 'react-icons/fa'
import ProfileForm from '../ProfileForm/ProfileForm'
import Context from '../../context/Context'
import firebase from '../../firebase'
import { Query } from 'react-apollo'
import { getUserName } from '../../apollo/queries'
import MyButton from '../MyButton/MyButton'
import Users from '../Users/Users'

const Profile = ({ scrollTop }) => {
  const { profileState, profileDispatch } = React.useContext(Context)
  const profileContainerRef = React.useRef(true)
  const [showProfile, setShowProfile] = React.useState(false)

  React.useEffect(() => {
    const unsubscribe = firebase.auth().onAuthStateChanged(user => {
      if (user) {
        profileDispatch({ type: 'LOGIN' })
        profileDispatch({ type: 'SET_ERROR', error: null })
        profileDispatch({ type: 'SET_CURRENT_USER_ID', userId: user.uid })
        profileDispatch({ type: 'SET_PROFILE_FORM_VISIBILITY', payload: false })
      } else {
        profileDispatch({ type: 'LOGOUT' })
      }
    })
    return () => {
      unsubscribe()
    }
  }, [])

  React.useEffect(() => {
    document.addEventListener('mousedown', handleMouseClick)
    return () => {
      document.removeEventListener('mousedown', handleMouseClick)
    }
  }, [])

  React.useEffect(() => {
    const timeout = setTimeout(() => {
      setShowProfile(true)
    }, 1000)
    return () => {
      clearTimeout(timeout)
    }
  }, [])
  const toggleProfileFormVisiability = () => {
    profileDispatch({
      type: 'SET_PROFILE_FORM_VISIBILITY',
      payload: !profileState.profileFormVisibility
    })
  }
  const handleClick = () => {
    toggleProfileFormVisiability()
  }
  const handleMouseClick = e => {
    profileContainerRef.current &&
      !profileContainerRef.current.contains(e.target) &&
      profileDispatch({ type: 'SET_PROFILE_FORM_VISIBILITY', payload: false })
  }
  const userIcon = <FaUser cursor="pointer" />

  return (
    <div
      className={
        profileState.userLoggedIn
          ? 'profile__container--loggedin'
          : 'profile__container--loggedout'
      }
      style={{
        top: profileState.userLoggedIn ? scrollTop : '',
        display: !showProfile && 'none'
      }}
      ref={profileContainerRef}
    >
      {profileState.userLoggedIn ? (
        <>
          <div className="profile__user-button">
            <MyButton
              className="profile__user-icon"
              onClick={handleClick}
              children={userIcon}
            />
            <Query
              query={getUserName}
              variables={{ id: profileState.currentUserId }}
            >
              {({ loading, data }) =>
                !loading &&
                data.user &&
                profileState.profileFormVisibility && (
                  <span>Hi {data.user.name} </span>
                )
              }
            </Query>
          </div>
          <Users visiable={profileState.profileFormVisibility} />
          {profileState.profileFormVisibility && (
            <ProfileForm scrollTop={scrollTop} />
          )}
        </>
      ) : (
        <ProfileForm />
      )}
    </div>
  )
}

export default Profile
