import React from 'react'
import './ProfileForm.css'
import Context from '../../context/Context'
import LoginForm from '../LoginForm/LoginForm'
import SignupForm from '../SignupForm/SignupForm'
import firebase from '../../firebase'
import { Mutation } from 'react-apollo'
import { deleteUserMutation } from '../../apollo/mutations'
import Loader from '../Loader/Loader'
import MyButton from '../MyButton/MyButton'
import ErrorMessage from '../ErrorMessage/ErrorMessage'
import Confirm from '../Confirm/Confirm'
const ProfileForm = () => {
  const { profileState, profileDispatch } = React.useContext(Context)
  const [deleteLoading, setDeleteLoading] = React.useState(false)
  const [logoutLoading, setlogoutLoading] = React.useState(false)
  const [showConfirm, setShowConfirm] = React.useState(false)
  React.useEffect(
    () => {
      profileDispatch({ type: 'SET_ERROR', error: null })
    },
    [profileState.currentForm]
  )
  const handleLogoutClick = async () => {
    setlogoutLoading(true)
    try {
      await firebase.auth().signOut()
    } catch (error) {
      profileDispatch({ type: 'SET_ERROR', error: error.message })
      setlogoutLoading(false)
    }
  }

  const deleteUser = async deleteUserMutation => {
    setDeleteLoading(true)
    try {
      await deleteUserMutation({
        variables: {
          id: profileState.currentUserId
        }
      })
      await firebase.auth().signOut()
    } catch (error) {
      const errorMessage = error.message
      profileDispatch({ type: 'SET_ERROR', error: errorMessage })
      setDeleteLoading(false)
    }
    profileDispatch({ type: 'LOGOUT' })
  }

  const handleConfirmClick = deleteUserMutation =>{
    deleteUser(deleteUserMutation)
    setShowConfirm(false)
  }
  const handleDeleteClick = () => {
    setShowConfirm(true)
  }
  const handleCancelClick = () => {
    setShowConfirm(false)
  }
  return (
    <div>
      {profileState.userLoggedIn ? (
        <Mutation mutation={deleteUserMutation}>
          {deleteUser =>
            deleteLoading ? (
              <Loader text="Deleting user..." />
            ) : logoutLoading ? (
              <Loader text="Loging out user..." />
            ) : (
              <div className="profile-form__buttons-container">
                <MyButton
                  text="Delete Me"
                  className="profile-form__button"
                  onClick={() => handleDeleteClick(deleteUser)}
                />
                {showConfirm && (
                  <Confirm
                    onCancelClick={handleCancelClick}
                    onConfirmClick={() => {
                      handleConfirmClick(deleteUser)
                    }}
                  />
                )}
                <MyButton
                  text="Log Out"
                  className="profile-form__button"
                  onClick={handleLogoutClick}
                />
              </div>
            )
          }
        </Mutation>
      ) : (
        <div className="profile-form__container">
          {profileState.currentForm === 'login' ? (
            <LoginForm />
          ) : (
            <SignupForm />
          )}
        </div>
      )}
      {profileState.error && <ErrorMessage error={profileState.error} />}
    </div>
  )
}

export default ProfileForm
