export const profileInitialState = {
  currentForm: 'login',
  userLoggedIn: false,
  error: null,
  currentUserId: '',
  profileFormVisibility: false
}

 export const profileReducer = (state, action) => {
  switch (action.type) {
    case 'LOGOUT':
      return {
        ...state,
        userLoggedIn: false,
        currentUserId: '',
        profileFormVisibility: false
      }
    case 'LOGIN':
      return { ...state, userLoggedIn: true }
    case 'SET_ERROR':
      return { ...state, error: action.error }
    case 'TOOGLE_FORM_TYPE':
      return {
        ...state,
        currentForm: state.currentForm === 'login' ? 'signup' : 'login'
      }
    case 'SET_CURRENT_USER_ID':
      return { ...state, currentUserId: action.userId }
    case 'SET_PROFILE_FORM_VISIBILITY':
      return { ...state, profileFormVisibility: action.payload }
    default:
      return state
  }
}
