import React from 'react'
import './LoginForm.css'
import Context from '../../context/Context'
import useInput from '../../hooks/useInput'
import firebase from '../../firebase'
import Loader from '../Loader/Loader'
import MyButton from '../MyButton/MyButton'

const LoginForm = () => {
  const { profileDispatch } = React.useContext(Context)
  const { reset: resetEmail, ...email } = useInput('')
  const { reset: resetPassword, ...password } = useInput('')
  const [loading, setLoading] = React.useState(false)
  const handleLoginClick = async () => {
    setLoading(true)
    try {
      await firebase
        .auth()
        .signInWithEmailAndPassword(
          email.value,
          password.value 
        )
    } catch (error) {
      const errorMessage = error.message
      profileDispatch({ type: 'SET_ERROR', error: errorMessage })
      setLoading(false)
    }
  }
  const handleSignupClick = () => {
    profileDispatch({ type: 'TOOGLE_FORM_TYPE' })
  }
  return (
    <div className="login-form__container">
      {loading ? (
        <Loader text="Loging in..." />
      ) : (
        <>
          <div className="login-form__header">Log In</div>
          <input placeholder="name" {...email} />
          <input placeholder="password" type="password" {...password} />
          <div className="login-form__buttons">
            <MyButton 
              onClick={handleSignupClick}
              text="Sign up"
              />
            <MyButton 
              onClick={handleLoginClick}
              text="Log in"
              />
          </div>
        </>
      )}
    </div>
  )
}

export default LoginForm
